/**
 *  axios API
 * axios Promise http 
 *  axios Promise
 */
import axios from 'axios'

//  axios 
const fetch = axios.create({
	baseURL: '/api/',
	time: 1000,
	withCredentials: true  //  cookie
})

//  Koa API
const user = {
	getUser: 'user',
	signin: 'signin',
	signup: 'signup',
	logout: 'logout'
}

const category = {
	findAll: 'cates',
	add: 'cates/add',
	edit: 'cates/edit',
	remove: 'cates/delete'
}

export {
	fetch,
	user,
	category
}


